    <hr class="my-2">
    <p><i18n path="common.guide.useNpm"></i18n></p>
    <showcode :trigger="false">
      <pre class="m-0">npm i -S v-selectpage</pre>
    </showcode>

    <p><i18n path="common.guide.include"></i18n></p>
    <showcode :trigger="false">
      <v-code lang="javascript">
        <pre>
        // use v-selectpage as a plugin
        import Vue from 'vue'
        import vSelectPage from 'v-selectpage'
        Vue.use(vSelectPage, {
          // server side data loader
          dataLoad: function(vue, data, params){
            return new Promise((resolve, reject)=>{
              axios.post(url, params).then(resp => resolve(resp), resp => reject(resp))
            })
          }
        })

        // use v-selectpage as a component
        import Vue from 'vue'
        import { SelectPage } from 'v-selectpage'
        // component
        export default {
          components: {
            'v-selectpage': SelectPage
          }
        }
        </pre>
      </v-code>
    </showcode>

    <div class="card p-4">
      <p><i18n path="common.globalConfig"></i18n></p>
      <ul>
        <li>title</li>
        <li>language</li>
        <li>placeholder</li>
        <li>pageSize</li>
        <li>dataLoad</li>
        <li>rtl</li>
      </ul>
    </div>

