      <v-code>
        <pre>
        &lt;template&gt;
          &lt;v-selectpage :data="list" key-field="id" show-field="name" &gt;
          &lt;/v-selectpage&gt;
        &lt;/template&gt;

        &lt;script&gt;
        export default {
          data () {
            return {
              list: [
                { id:1, name: 'Chicago Bulls', desc: '芝加哥公牛' },
                { id:2, name: 'Cleveland Cavaliers', desc: '克里夫兰骑士' },
                { ... }
              ]
            }
          }
        }
        &lt;/script&gt;
        </pre>
      </v-code>
    </showcode>

