    <h4 class="mt-5"><i18n path="selectpage.demo.custom"></i18n></h4>
    <p>
      <button type="button" class="btn btn-danger mr-2" @click="disabled=true"><i18n path="selectpage.demo.btnDisabled"></i18n></button>
      <button type="button" class="btn btn-outline-secondary" @click="disabled=false"><i18n path="selectpage.demo.btnEnabled"></i18n></button>
    </p>
    <p>
      <v-selectpage :data="sample" :show-field="renderRow" :disabled="disabled">
      </v-selectpage>
    </p>
    <showcode>
      <v-code>
        <pre>
        &lt;!-- control buttons --&gt;
        &lt;button type="button" @click="disabled=true"&gt;Disabled&lt;/button&gt;
        &lt;button type="button" @click="disabled=false"&gt;Enabled&lt;/button&gt;

        &lt;v-selectpage :data="sample" :show-field="renderRow" :disabled="disabled"&gt;
        &lt;/v-selectpage&gt;

        &lt;script&gt;
        export default {
          data () {
            return {
              disabled: false,
              renderRow (row) {
                let result = ''
                if(row && Object.keys(row).length)
                  result = `[&lt;b&gt;${row.id&lt;10?'0'+row.id:row.id}&lt;/b&gt;] - ${row.name} (${row.desc})`
                return result
              }
            }
          }
        }
        &lt;/script&gt;
        </pre>
      </v-code>
    </showcode>

    <h4 class="mt-5"><i18n path="selectpage.demo.nopage"></i18n></h4>
