              v-model="server"
              :result-format="resultFormat">
      </v-selectpage>
    </p>
    <showcode>
      <v-code>
        <pre>
        &lt;v-selectpage
          data="yourserver/load"
          key-field="id"
          show-field="name"
          search-field="userName"
          v-model="initValue"
          :result-format="resultFormat"&gt;
        &lt;/v-selectpage&gt;

        &lt;script&gt;
        export default {
          data(){
            return {
              sample1: [
                {id:1 ,name:'The People\'s Republic of China',desc:'中华人民共和国'},
                {id:2 ,name:'United States of America',desc:'美利坚合众国'},
                {...}
              ],
              showFields:[
                {title: 'id',data: 'id'},
                {title: 'name',data: 'name'},
                {title: 'desc',data: 'desc'},
              ]
            }
          },
          methods: {
            resultFormat(resp){
              if(resp && resp.data) return resp.data.values.gridResult
            }
          }
        }
        &lt;/script&gt;
        </pre>
      </v-code>
    </showcode>

