    <h2 class="mt-5">API</h2>
    <hr class="my-2">
    <p><i18n path="selectpage.api.desc"></i18n></p>
    <showcode :trigger="false">
      <v-code lang="javascript">
        <pre>
        &lt;v-selectpage :data="data" ref="sp" &gt;

        &lt;script&gt;
        //call api
        this.$refs.sp.remove()
        &lt;/script&gt;
        </pre>
      </v-code>
    </showcode>
    <table class="table table-striped">
      <thead>
      <tr>
        <th><i18n path="common.tbTitle.param"></i18n></th>
        <th><i18n path="common.tbTitle.type"></i18n></th>
        <th><i18n path="common.tbTitle.desc"></i18n></th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td>remove</td>
        <td></td>
